.body {
  background-color: #0e0e0e;
  background-repeat: repeat;
  background-size: contain;

  @media screen and (max-width: 767px) {
    background-position: left 0 top 364px;

    background-image: url(../images/mobile/bg_body-mobile.png);

    @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
      .body {
        background-image: url(../images/mobile/bg_body-mobile@2x.png);
      }
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    background-position: left 0 top 364px, center;

    background-image: url(../images/tablet/bg_body-tablet.png),
      url(../images/tablet/decor-tablet.png);

    @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
      .body {
        background-image: url(../images/tablet/bg_body-tablet@2x.png);
      }
    }
  }

  @media screen and(min-width: 1280px) {
    background-position: center, center;
    background-size: cover, 1280px;
    // background-position: center, center top 100px;
    // background-size: cover, 1280px;
    background-image: url(../images/desktop/bg_body-desktop.png),
      url(../images/desktop/decor-desktop.png);

    @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
      .body {
        background-image: url(../images/desktop/bg_body-desktop@2x.png);
      }
    }
  }
}