.header__title {
  margin-top: 78px;
  margin-bottom: 25px;

  text-align: center;

  @include for-size(tablet-up) {
    padding-top: 40px;
    margin-bottom: 27px;
  }

  @include for-size(desktop-up) {
    padding-top: 40px;
    margin-top: 0px;
    margin-bottom: 52px;
  }
}

.header__title--decor {
  max-width: 215px;
  margin-right: auto;
  margin-left: auto;
  color: #fee;
  font-family: var(--font);
  font-weight: 900;
  font-size: 18px;
  line-height: 1.22;
  text-shadow: 0 -40px 100px, 0 0 2px, 0 0 1em rgb(255, 13, 191), 0 0 0.5em rgb(255, 13, 191),
    0 0 0.1em rgb(255, 13, 191), 0 10px 3px #000;

  @include for-size(tablet-up) {
    max-width: 355px;
    /* margin-top: 40px; */
    font-size: 30px;
  }

  @include for-size(desktop-up) {
    max-width: 590px;
    font-size: 50px;
  }
}

.form {
  width: 100%;
  margin-top: 25px;
  margin-bottom: 25px;
}

.form-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 130px;

  @include for-size(phone-only) {
    flex-direction: column;
  }

  @include for-size(phone-plus-up) {
    flex-direction: column;
  }

  @include for-size(tablet-up) {
    flex-direction: row;
  }
}

.search-btn {
  cursor: pointer;
  position: absolute;
  padding: 10px;
  top: 5%;
  left: 80%;
  width: 34px;
  height: 34px;
  background-color: transparent;
  border: none;
  outline: none;

  @include for-size(tablet-up) {
    top: 5%;
    left: 73%;
  }
}

.form__input {
  width: 270px;
  height: 40px;
  padding: 10px 30px;
  font-size: 16px;
  line-height: 1.25;
  color: var(--second-text-color);
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  outline: none;
  border: none;

  &:hover,
  &:focus {
    background-color: rgba(255, 255, 255, 0.05);
    color: white;
  }
}

.form__input:first-child {
  @include for-size(phone-only) {
    margin-bottom: 25px;
  }

  @include for-size(phone-plus-up) {
    margin-bottom: 25px;
  }

  @include for-size(tablet-up) {
    margin-right: 30px;
  }
}

.input-wrapper {
  position: relative;
}

.form__icon {
  position: absolute;

  top: 30%;
  left: 30%;
  fill: #939393;
  transform: matrix(-1, 0, 0, 1, 0, 0);

  @include for-size(tablet-up) {
    top: 35%;
    left: 30%;
  }
}

.all-countries {
  background-color: aquamarine;
}

// Анимация для Заголовка

.animate__animated.animate__backInDown {
  --animate-duration: 2s;
}

//стилі для селекту
.input-country {
  background-color: #d0cccc;
  color: #dc56c5;
}